@import "../../GlobalStyles/GlobalStyles.scss";
.swiper {
    min-width: 640px;
    min-height: 360px;
    border-radius: 5px;
    @media screen and (max-width: 768px) {
        width: 100%;
        height: 100%;
        min-width: unset;
        min-height: unset;
    }

    // @media screen and (min-width: 1700px) {
    //     width: 100%;
    //     height: 100%;
    // }
    :global(.swiper-pagination-bullet-active) {
        background-color: $fadeClr300;
        width: 20px;
        height: 3px;
        border-radius: 0.5rem;
        opacity: 1;
        @media screen and (max-width: 768px) {
            height: 5px;
        }
    }

    :global(.swiper-pagination-bullet) {
        background: $fadeClr;
        width: 15px;
        height: 3px;
        border-radius: 0.5rem;
        opacity: 0.8;
        transition: 0.25s ease;
        @media screen and (max-width: 768px) {
            height: 3px;
        }
    }
    :global(.swiper-pagination) {
        margin-bottom: 15px !important;
        @media screen and (max-width: 768px) {
            margin-bottom: 15px !important;
        }
    }
    :global(.swiper-pagination-bullet-active) {
        background-color: $fadeClr300;
        width: 20px;
        height: 3px;
        border-radius: 0.5rem;
        opacity: 1;
        @media screen and (max-width: 768px) {
            height: 5px;
        }
    }
    // background-color: red;
    & .swiper_item {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover !important;
        }
    }
}
